

































import Vue from 'vue';
import menuData from '@/assets/data/menuData.json';
import { mapState } from 'vuex';
export default Vue.extend({
    name: 'VFooter',
    data() {
        return {
            menuList: menuData,
            isShowCode: false
        }
    },
    computed: {
        ...mapState(['companyData'])
    }
});
